body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Login {
  font-family: sans-serif;
  display: flex; 
  justify-content: center; 
  align-items: center;
  margin: 155px auto;
}

.Register {
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 155px auto;
}

.RegisterForm {
  background-color: rgb(250,251,250);
  border: 1px solid black;
  max-width: 900px;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px;

}

.LoginForm {
  background-color: rgb(250,251,250);
  border: 1px solid black;
  max-width: 600px;
  width: 100%;
  margin: 10px auto;
  padding: 50px;
}

label,
input {
  display: block;
  width: 95%;
}

label {
  height: 22px;
}

input {
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid #777;
}

input.error {
  border-color: red;
}

.input-feedback {
  color: rgb(235, 54, 54);
  margin-top: -15px;
  font-size: 14px;
}

.registerButton {
  padding: 10px 15px;
  width: 90%;
  border-radius: 20px;
  background-color: rgb(91, 109, 91);
  color: white;
  border: 1px solid rgb(91, 109, 91);
  background-color: 250ms;
  display: block; 
}

.registerButton:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(91, 109, 91);
}

.loginButton {
  padding: 10px 15px;
  width: 100%;
  border-radius: 20px;
  background-color: rgb(91, 109, 91);
  color: white;
  border: 1px solid rgb(91, 109, 91);
  background-color: 250ms;
  display: block; 
}

.loginButton:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(91, 109, 91);
}

