  margin: 10px auto 20px;
  display: flex;
  justify-content: center; 
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
  display: flex;
  flex-direction: column;
}

.column {
  height: auto;
  width: 80vw;
  margin: 20px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.first-column {
  background-color: #ffffff;
  border-bottom: 1px solid grey;
}

.second-column {
  background-color: #a2bea2;
  min-height: 50px;
}

.movable-item {
  background-color: #ffffff;
  height: 30px;
  width: 160px;
  margin: 10px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid grey;
  font-size: small;
  text-align: center;
}

.pointer {
  width: 150px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background: #5b6d5b;
  font-size: 18px;
  color: #ffffff;
}

.pointer:before {
  content: "";
  position: absolute;
  right: -15px;
  bottom: 0;
  width: 0;
  height: 0;
  border-left: 15px solid #5b6d5b;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
}

